.App {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  font-family: 'Open Sans', sans-serif;
}

.calculator{
  background-image:
  linear-gradient(
    to bottom right, 
    #e5473d, #df6158 
  );  
  width: 85%;
}

.chronometer{
  background-image:
  linear-gradient(
    to bottom right, 
    #101a40, #414f87 
  );  
  width: 85%;
}

.about{
  background-image:
  linear-gradient(
    to bottom right, 
    #101a40, #414f87 
  );  
  width: 85%;
}

.menu{
  background-color: #20201f;
  color: #f2eeeb;
  height: 100vh;
  width: 15%;
  z-index: 10;

  div{
    display: flex;
    flex-direction: row;
    padding: 20px;
    border-bottom: 1px solid #f3f1ee;

    h1{
      font-size: 20px;
      margin: 0;
      padding-top: 3px;
      padding-left: 20px;
    }

    img {
      animation: spin infinite 6s linear;
      height: 30px;
    }
    
    @keyframes spin {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(-360deg);
      }
    }
    
  }

  a{
    text-decoration: none;    
  }

  h3{
    margin: 0;
    text-decoration: none;
    color: #a1a1a0;
    padding: 20px;
    background: linear-gradient(to right, #47c0c0 50%, #20201f 50%);
    background-size: 200% 100%;
    background-position: right bottom;
    transition: 0.5s ease-out;
    font-size: 16px;

    &:hover{
      color: black;
      background-position: left bottom;
    }

  }
  
}

.calculator-container{
  height: 80vh;
  width: 50vw;
  margin-top: 70px;
  margin-left: 200px;
}

.keypad{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 60%;

  button{
    width: 23%;
    height: 25%;
    font-size: 26px;
    margin: 5px;
    border: .1rem solid #000;
    border-radius: .6rem;
    background-color: #fdb430;
    background-image: url(data:image/png;base64,); 
    box-shadow: 0.2rem 0.2rem black;
    color: white;
  }

  button:hover{
    box-shadow: none;

  }

  button:focus{
    outline: none;
  }

  .big{
    width: 47.5%;
  }
}

.screen{
  
  height: 20%;
  width: 97%;
  background-image:
  linear-gradient(
    to bottom , 
    #101a40, #414f87
  );  
  font-size: 40px;
  text-align: right;
  border: .1rem solid white;
  border-radius: .6rem;

  p{
    padding-right: 20px;
    color: white;
    margin-top: 25px;
  }
}

.chrono-container{
  background-color: #e5473d;
  background-image: url(data:image/png;base64,); 
  margin-top: 100px;
  margin-left: 200px;
  padding: 100px;
  width: 40vw;
  border-radius: 20px;
  border: 5px solid #fdb430;
  text-align: left;

  .chrono-numbers{
    margin: 0;
    font-size: 110px;
    font-family: 'Orbitron';
    color: white;
    text-shadow: 2px 2px 4px black;
  }

  .chrono-buttons{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    margin-top: 50px;
  }

  .start-button{
    background-image: url(./Images/start.svg);
    background-color: rgba(0,0,0,0);
    border: none;
    border-radius: 50%;
    box-shadow: 2px 2px rgba(0, 0, 0, 0.5);
    width: 100px;
    height: 100px;
    transition-duration: 0.3s;
  }

  .stop-button{
    background-image: url(./Images/pause.svg);
    background-color: rgba(0,0,0,0);
    border: none;
    border-radius: 50%;
    box-shadow: 2px 2px rgba(0, 0, 0, 0.5);
    width: 100px;
    height: 100px;
    transition-duration: 0.3s;
  }

  .reset-button{
    background-image: url(./Images/reset.svg);
    background-color: rgba(0,0,0,0);
    border: none;
    border-radius: 50%;
    box-shadow: 2px 2px rgba(0, 0, 0, 0.5);
    width: 100px;
    height: 100px;
    transition-duration: 0.3s;
  }
  button:hover{
    transform: scale(1.1); 
    cursor: pointer;
  }

  button:focus{
    outline: none;
  }
}

.info-container{
  border: 1px solid white;
  border-radius: 5px;
  width: 800px;
  height: 60vh;
  margin-left: 150px;
  margin-top: 100px;
  color: white;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
}

.left-info{
  width: 50%;
  background-color: #fdb430;
  border-radius: 5px;
  background-image: url(data:image/png;base64,); 
  
  h3{
    padding-top: 30px;
    padding-left: 30px;
    margin-top: 0px;
  }

  img{
    height: 50px;
  }

  div{
    display: flex;
    flex-direction: row;
    padding-top: 15px;
    padding-left: 30px;
    p{
      padding-left: 20px;
    }
  }
}

.right-info{
  height: 100%;
  width: 50%;
  text-align: left;
  background-color: #e5473d;

  background-image: url(data:image/png;base64,); 

  .contact-logos{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding-top: 10px;
    img{
      height: 60px;
      padding-right: 30px;
    }
  }

  .contact-info{
    h2{
      margin: 0;
    }

    padding-top: 30px;
    padding-left: 30px;
  }
}

@media only screen and (max-width: 768px) {
  .App {
    display: initial;
  }

  .menu{
    background-color: #20201f;
    color: #f2eeeb;
    height: 70px;
    width: 100vw;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    position: fixed;
    bottom: 0;
    left: 0;
  
    div{
      position: fixed;
      top: 5px;
      left: 5px;
      border: 0;
    }
  
    a{
      text-decoration: none;    
    }
  
    h3{
      margin: 0;
      text-decoration: none;
      color: #a1a1a0;
      padding: 5px;
      background: none;
      font-size: 12px;
      padding-top: 12px;
    }

    h3:hover{
      color:#a1a1a0;
    }
    
  }

  .calculator{
    width: 100%;
    height: 100vh;
  }

  .calculator-container{
    
    margin: 0;
    padding: 0px;
    width: 100%;
    height: 80%;
    position: absolute;
    top: 28%;
    margin-top: -50px;
  }

  .screen{
    margin: 0;
    height: 13%;
    width: 80%;
    font-size: 30px;  
    margin-left: 9vw;
    margin-bottom: 10px;

    p{
      padding-right: 20px;
      color: white;
      margin-top: 10px;
    }
  }

  .keypad{
    display: flex;
    flex-wrap: wrap;
    margin-left: 9vw;
    width: 85%;
    height: 60%;
  
    button{
      width: 22%;
      height: 17%;
      margin: 3px;
    }
    .big{
      width: 46%;
    }
  }

  .about{
    width: 100%;
    height: 100vh;
  }

  .info-container{
    border: 0;
    width: 100%;
    height: 100vh;
    margin: 0;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
  }

  .left-info{
    width: 100%;
    border: 0;
    border-radius: 0;
    padding-bottom: 20px;
  
    h3{
      padding-top: 80px;
      padding-left: 30px;
      margin-top: 0px;
    }
  
    img{
      height: 50px;
    }
  
    div{
      display: flex;
      flex-direction: row;
      padding-top: 20px;
      padding-left: 110px;
      p{
        padding-left: 20px;
      }
    }
  }

  .right-info{
    width: 100%;
    border: 0;
    text-align: center;
    
    .contact-logos{
      display: flex;
      flex-direction: row;
      justify-content: center;
      padding: 0;
      padding-top: 5px;
      padding-left: 15px;
    
      img{
        height: 30px;
      }
    }
  
    .contact-info{
      h2{
        margin: 0;
      }
  
      padding: 0;
      padding-top: 20px;
    }
  }

  .chronometer{
    width: 100%;
    height: 100vh;
  }

  .chrono-container{
    margin: 0 auto;
    padding: 0;
    width: 70%;
    height: 40%;
    position: absolute;
    top: 35%;
    left: 15%;
    margin-top: -50px;
  
    .chrono-numbers{
      margin: 0;
      margin-left: 5px;
      margin-top: 45px;
      font-size: 44px;
      font-family: 'Orbitron';
      color: white;
      text-shadow: 2px 2px 4px black;
    }
  
    .chrono-buttons{
      display: flex;
      flex-direction: row;
      width: 80%;
      justify-content: space-between;
      margin: 50px auto;
      height: 200px;
    }
  
    .start-button{
      width: 50px;
      height: 50px;
    }
  
    .stop-button{
      width: 50px;
      height: 50px;
    }
  
    .reset-button{
      width: 50px;
      height: 50px;
    }
  }

}